// eslint-disable-next-line import/no-extraneous-dependencies
import { request } from "../utils";
import {
    RequestRestNewsletterSubscribePost,
    ParamsRestNewsletterSubscribePost,
    ResponseRestNewsletterSubscribePost,
    locationRestNewsletterSubscribePost
} from "../wp-api";

async function subscribe(data: RequestRestNewsletterSubscribePost) {
    await request<
        RequestRestNewsletterSubscribePost,
        ParamsRestNewsletterSubscribePost,
        ResponseRestNewsletterSubscribePost
    >({
        location: locationRestNewsletterSubscribePost,
        request: data
    });
}

export { subscribe };
